import { useState, useEffect } from 'react';
import '../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Routes, Route, useNavigate } from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import { Container, Row, Col } from 'react-bootstrap';
import Detail from './detail';
import axios from 'axios';
import Form from 'react-bootstrap/Form';
import Pagination from "react-js-pagination";


function Incident() {
  // 데이터 호출 시작. DB 서버 데이터 호출 대신 사용
  let navigate = useNavigate();
  let [data, setData] = useState([]);
  let [query00, setQuery00] = useState([]);
  let [query01, setQuery01] = useState([]);
  let [query02, setQuery02] = useState([]);
  let [query03, setQuery03] = useState([]);
  let [query04, setQuery04] = useState([]);
  let [itemcount, setItemcount] = useState(120);
  let [type, setType] = useState("");
  const [page, setPage] = useState(1);
  const [items, setItems] = useState(10);
  const stateChange = (e) => {
    console.log(e.target.value);
    setType(e.target.value);
  }
  const handlePageChange = (page) => { setPage(page); };
    console.log(items*(page-1), items*(page-1)+items);

  useEffect(()=>{
    (async () =>{
    axios.get('https://violetleaf44.github.io/react/data/indata.json').then((response)=>{
       setQuery00(response.data);  
       setData(response.data);  
        })
        .catch(()=>{
          console.log('failed')
        });
    axios.get('https://violetleaf44.github.io/react/data/inquery01.json').then((response)=>{
      setQuery01(response.data);  
    })
    .catch(()=>{
      console.log('failed')
    });
    axios.get('https://violetleaf44.github.io/react/data/inquery02.json').then((response)=>{
      setQuery02(response.data);  
      })
      .catch(()=>{
        console.log('failed')
      });
    axios.get('https://violetleaf44.github.io/react/data/inquery03.json').then((response)=>{
      setQuery03(response.data);  
      })
      .catch(()=>{
        console.log('failed')
      });
      axios.get('https://violetleaf44.github.io/react/data/inquery04.json').then((response)=>{
      setQuery04(response.data);  
      })
      .catch(()=>{
        console.log('failed')
      });

      }
    )();
  },[])
  // 데이터 호출 끝

 return (
   <div>
     <Container className='sr_table'>
       {/* 검색 시작*/}
       <Row>
         <Col className='th_ser'><span className='ser_bar'>|</span> 검색</Col>
       </Row>
       <Row className='sr_text'>
         <Col md="1">요청번호</Col>
         <Col md="3">
           <Form.Group className="mb-3">
             <Form.Control placeholder="Disabled input" disabled />
           </Form.Group>
         </Col>
         <Col md="1">상세유형</Col>
         <Col md="3">
          <Form.Group className="mb-3">
            <Form.Select id="srType" onChange={stateChange}>
              <option value="01">서버</option>
              <option value="02">네트워크</option>
              <option value="03">APP</option>
              <option value="04">OA</option>
              <option value="00" selected="selected">-- 초기화 --</option>
            </Form.Select>
           </Form.Group>
           </Col>
         <Col md="1">요청자</Col>
         <Col md="3">
         <Form.Group className="mb-3">
             <Form.Control placeholder="Disabled input" disabled />
           </Form.Group>
          
         </Col>
       </Row>
       <Row className='sr_text'>
         <Col md="1">요청제목</Col>
         <Col md="11">
           <Form.Group className="mb-3">
             <Form.Control placeholder="Disabled input" disabled />
           </Form.Group>
         </Col>
       </Row>
       <Row className='sr_text'>
         <Col md="1">요청상태</Col>
         <Col md="3">
          <Form.Group className="mb-3">
            <Form.Control placeholder="Disabled input" disabled />
           </Form.Group>
          </Col>
         <Col md="1">요청일</Col>
         <Col md="3">
         <Form.Group className="mb-3">
           <Form.Control placeholder="Disabled input" disabled />
         </Form.Group>
         </Col>
         <Col md="1">완료일</Col>
         <Col md="3">
         <Form.Group className="mb-3">
           <Form.Control placeholder="Disabled input" disabled />
         </Form.Group>
         </Col>
         <Col md="4"></Col>
       </Row>
       <Row>
         <Col md="4"></Col>
         <Col md="4" className='btn_inbox'><Button variant="primary" onClick={()=>{
            setPage(1);
            switch(type){
              case '01':
                setData(query01);
                setItemcount(query01.length);
                break;
              case '02':
                setData(query02);
                setItemcount(query02.length); 
                break;
              case '03':
                setData(query03);
                setItemcount(query03.length);
                break;
              case '04':
                setData(query04);
                setItemcount(query04.length);
                break;
              case '00':
                setData(query00);
                setItemcount(query00.length);
                break;
            }; 
           
           console.log(itemcount)}}>검색</Button></Col>
         <Col md="4"></Col>
       </Row>
       <Row className='row_line'>
       <Col className='th_ser'><span className='ser_bar'>|</span> 검색결과</Col>
       </Row>
     </Container>
     {/* 검색 끝 */}

     {/* 라우트 시작 */}
     <Routes>
     <Route path="/*" element={
          <div>
          <Container>
                 
            <Row className="paging_box">
              <Col className="paging_text_right" md="1">요청번호</Col>
              <Col className="paging_text_right" md="1">요청유형</Col>
              <Col className="paging_text_right" md="1">요청자</Col>
              <Col md="4">요청제목</Col>
              <Col className="paging_text_right" md="1">요청상태</Col>
              <Col className="paging_text_center" md="2">요청일시</Col>
              <Col className="paging_text_center" md="2">종료일시</Col>
            </Row>
             
          {data.slice(10*(page-1), 10*(page-1)+10
          ).map((v,i) => {
            return (
              <Row className="paging_text" onClick={()=>{navigate('/incident/detail/'+ v.id)}}>
                <Col md="1">{v.sr_id}</Col>
                <Col md="1">{v.type}</Col>
                <Col md="1">{v.name}</Col>
                <Col className="paging_text_left" md="4">{v.title}</Col>
                <Col md="1">{v.state}</Col>
                <Col className="paging_text_center" md="2">{v.startday}</Col>
                <Col className="paging_text_center" md="2">{v.endday}</Col>
            </Row>
            )
          })}
          </Container>
    
          <Pagination
            activePage={page}
            itemsCountPerPage={10}
            totalItemsCount={itemcount}
            pageRangeDisplayed={5}
            prevPageText={"‹"}
            nextPageText={"›"}
            onChange={handlePageChange} 
            />
        </div>
        } />;
       <Route path="/detail/:id" element={<Detail data={data} />} />;
       <Route path="*" element={<div>404 없는 페이지입니다.</div>} />
     </Routes>
     {/* 라우트 끝 */}

   </div>
 );
}

export default Incident